<template>
  <div class="card">
    <div class="card-header">Informações do cliente</div>
    <div class="card-body">
      <h5 class="card-title">Esses são os detalhes do cliente:</h5>
      <p class="list-group-item">Nome: {{ client.name }}</p>
      <p class="list-group-item">CPF: {{ client.cpf }}</p>
      <p class="list-group-item">Telefone: {{ client.phone }}</p>
      <p class="list-group-item">
        Data de Nascimento:
        {{ new Date(client.birth_date).toLocaleDateString("pt-br") }}
      </p>
      <router-link to="/" class="btn btn-primary">Voltar</router-link>
    </div>
  </div>
</template>

<script>
import ClientDataService from "../services/ClientDataService";

export default {
  name: "client",
  data() {
    return {
      client: {
        id: null,
        name: "",
        cpf: "",
        phone: "",
        birth_date: "01-01-2021",
      },
      submitted: false,
    };
  },
  methods: {
    retrieveClient(id) {
      ClientDataService.get(id)
        .then((response) => {
          this.client = response.data.data;
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.retrieveClient(this.$route.params.id);
  },
};
</script>

<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>